'use client';

import { useState, useEffect } from "react";
import Header from "@/src/components/landingpage/Header";
import Footer from "@/src/components/landingpage/Footer";
import UserTopMenu from "@/src/components/landingpage/UserTopMenu";
import NotificationDialog from "@/src/components/ui/NotificationDialog";
import { useUser } from "@/src/context/AuthContext";
import { useVehicleIssues } from "@/src/hooks/useVehicleIssues";
import { ExtendedVehicleIssue } from "@/src/lib/vehicleIssues/vehicleIssueTypes";
import { Button } from "@/src/components/ui/button";
import VehicleIssueTable from "@/src/components/vehicleIssues/VehicleIssueTable";
import AssignTechnicianForm from "@/src/components/vehicleIssues/AssignTechnicianForm";
import { db } from "@/src/firebaseConfig";
import { collection, getDocs, query, where, doc, getDoc } from "firebase/firestore";
import { Input } from "@/src/components/ui/input";
import { SimpleSelect } from "@/src/components/ui/select";

export default function VehicleIssuesManagementPage() {
  const { role, companyId, loading } = useUser();
  const normalizedRole = role?.toLowerCase();
  const isAdmin = normalizedRole === "admin";

  const canViewIssues = isAdmin || (!!companyId && ["company_owner", "company_admin", "technician"].includes(normalizedRole || ""));

  const [editingIssue, setEditingIssue] = useState<ExtendedVehicleIssue | null>(null);
  const [showForm, setShowForm] = useState(false);
  const [dialog, setDialog] = useState({ open: false, type: "info" as "success" | "error" | "info", title: "", description: "", onConfirm: undefined as (() => void) | undefined });
  const [stationMap, setStationMap] = useState<Record<string, string>>({});
  const [companyNames, setCompanyNames] = useState<Record<string, string>>({});
  const [filterStatus, setFilterStatus] = useState<string | undefined>(undefined);
  const [search, setSearch] = useState<string>("");

  const {
    issues,
    loading: issuesLoading,
    handleUpdate,
    loadMore,
    hasMore,
  } = useVehicleIssues(
    canViewIssues
      ? {
          role: role ?? undefined,
          companyId: companyId ?? undefined,
          status: filterStatus,
          search: search,
          pageSize: 20,
        }
      : undefined
  );

  useEffect(() => {
    if (!loading && canViewIssues) {
      if (isAdmin) {
        loadAllCompanyNames();
        loadAllStations();
      } else if (companyId) {
        loadCompanyName(companyId);
        loadStations(companyId);
      }
    }
  }, [loading, companyId, isAdmin, canViewIssues]);

  const loadCompanyName = async (id: string) => {
    const snap = await getDoc(doc(db, "rentalCompanies", id));
    if (snap.exists()) setCompanyNames((prev) => ({ ...prev, [id]: snap.data().name }));
  };

  const loadAllCompanyNames = async () => {
    const snapshot = await getDocs(collection(db, "rentalCompanies"));
    const map: Record<string, string> = {};
    snapshot.docs.forEach((doc) => { map[doc.id] = doc.data().name; });
    setCompanyNames(map);
  };

  const loadStations = async (companyId: string) => {
    const snapshot = await getDocs(query(collection(db, "rentalStations"), where("companyId", "==", companyId)));
    const map: Record<string, string> = {};
    snapshot.docs.forEach((doc) => { map[doc.id] = doc.data().name; });
    setStationMap(map);
  };

  const loadAllStations = async () => {
    const snapshot = await getDocs(collection(db, "rentalStations"));
    const map: Record<string, string> = {};
    snapshot.docs.forEach((doc) => { map[doc.id] = doc.data().name; });
    setStationMap(map);
  };

  const showDialog = (type: "success" | "error" | "info", title: string, description = "", onConfirm?: () => void) => {
    setDialog({ open: true, type, title, description, onConfirm });
  };

  const handleAssignTechnician = async (userId: string) => {
    if (!editingIssue) return;

    try {
      await handleUpdate(editingIssue.id, {
        ...editingIssue,
        assignedTechnicianId: userId,
      });

      showDialog("success", "Technician assigned successfully");
      setShowForm(false);
      setEditingIssue(null);
    } catch {
      showDialog("error", "Failed to assign technician");
    }
  };

  if (loading) return <div className="text-center py-10">Loading...</div>;
  if (!canViewIssues) return <div className="text-center py-10 text-red-500">You do not have permission to view this page.</div>;

  return (
    <div className="flex flex-col min-h-screen">
      <Header />
      <UserTopMenu />

      <main className="flex-1 p-6 space-y-6">
        <div className="flex items-center justify-between">
          <h1 className="text-2xl font-bold text-gray-800">{isAdmin ? "üõ†Ô∏è All Vehicle Issues (Admin)" : "üõ†Ô∏è Vehicle Issues Management"}</h1>
        </div>

        {/* Filter and Search */}
        <div className="flex items-center gap-4">
          <SimpleSelect
            value={filterStatus || ""}
            onChange={(val) => setFilterStatus(val || undefined)}
            placeholder="Filter Status"
            options={[
              { value: "", label: "All" },
              { value: "open", label: "Open" },
              { value: "resolved", label: "Resolved" },
            ]}
          />
          <Input
            placeholder="Search VIN or Technician"
            value={search}
            onChange={(e) => setSearch(e.target.value)}
          />
        </div>

        {/* Issues Table */}
        <VehicleIssueTable
          issues={issues}
          onEdit={(issue) => {
            setEditingIssue(issue);
            setShowForm(true);
          }}
        />

        {/* Load More */}
        {hasMore && (
          <div className="text-center">
            <Button onClick={loadMore}>Load More</Button>
          </div>
        )}

        {/* Assign Form */}
        {showForm && editingIssue && (
          <div>
            <h2 className="text-xl font-semibold">Assign Technician</h2>

            <AssignTechnicianForm
              companyId={companyId || ""}
              onAssign={handleAssignTechnician}
            />

            <Button variant="ghost" onClick={() => { setShowForm(false); setEditingIssue(null); }} className="w-full">
              Cancel
            </Button>
          </div>
        )}
      </main>

      <Footer />

      <NotificationDialog
        open={dialog.open}
        type={dialog.type}
        title={dialog.title}
        description={dialog.description}
        onClose={() => setDialog((prev) => ({ ...prev, open: false }))}
        onConfirm={dialog.onConfirm}
      />
    </div>
  );
}
