import { useEffect, useState } from 'react';
import { ExtendedVehicleIssue } from '@/src/lib/vehicleIssues/vehicleIssueTypes';
import { db } from '@/src/firebaseConfig';
import {
  collection,
  onSnapshot,
  query,
  where,
  doc,
  updateDoc,
  orderBy,
  limit,
  startAfter,
  getDocs,
  QueryDocumentSnapshot,
  DocumentData,
} from 'firebase/firestore';

interface Options {
  role?: string;
  companyId?: string;
  status?: string; // ‚úÖ "open" | "resolved" | undefined
  search?: string; // ‚úÖ VIN ho·∫∑c technicianId search
  pageSize?: number; // ‚úÖ default 20
}

export function useVehicleIssues(options?: Options) {
  const [issues, setIssues] = useState<ExtendedVehicleIssue[]>([]);
  const [loading, setLoading] = useState(true);
  const [lastDoc, setLastDoc] = useState<QueryDocumentSnapshot<DocumentData> | null>(null);
  const [hasMore, setHasMore] = useState(true);

  const normalizedRole = options?.role?.toLowerCase();
  const isAdmin = normalizedRole === 'admin';
  const companyId = options?.companyId;
  const status = options?.status;
  const search = options?.search?.toLowerCase() || "";
  const pageSize = options?.pageSize || 20;

  useEffect(() => {
    if (!isAdmin && !companyId) {
      setIssues([]);
      setLoading(false);
      return;
    }

    let q = isAdmin
      ? query(collection(db, 'vehicleIssues'))
      : query(collection(db, 'vehicleIssues'), where('companyId', '==', companyId!));

    // ‚úÖ Filter by status n·∫øu c√≥
    if (status) {
      q = query(q, where('status', '==', status));
    }

    // ‚úÖ Order v√† gi·ªõi h·∫°n
    q = query(q, orderBy('reportedAt', 'desc'), limit(pageSize));

    const unsubscribe = onSnapshot(q, (snapshot) => {
      const fetchedIssues: ExtendedVehicleIssue[] = snapshot.docs.map((doc) => ({
        ...(doc.data() as ExtendedVehicleIssue),
        id: doc.id,
      }));

      setIssues(fetchedIssues);
      setHasMore(snapshot.docs.length === pageSize);
      setLastDoc(snapshot.docs[snapshot.docs.length - 1] || null);
      setLoading(false);
    });

    return () => unsubscribe();
  }, [isAdmin, companyId, status, pageSize]);

  // ‚úÖ Load th√™m trang
  const loadMore = async () => {
    if (!lastDoc || !hasMore) return;

    let q = isAdmin
      ? query(collection(db, 'vehicleIssues'))
      : query(collection(db, 'vehicleIssues'), where('companyId', '==', companyId!));

    if (status) {
      q = query(q, where('status', '==', status));
    }

    q = query(q, orderBy('reportedAt', 'desc'), startAfter(lastDoc), limit(pageSize));

    const snap = await getDocs(q);
    const newIssues: ExtendedVehicleIssue[] = snap.docs.map((doc) => ({
      ...(doc.data() as ExtendedVehicleIssue),
      id: doc.id,
    }));

    setIssues((prev) => [...prev, ...newIssues]);
    setHasMore(snap.docs.length === pageSize);
    setLastDoc(snap.docs[snap.docs.length - 1] || null);
  };

  // ‚úÖ Update issue
  const handleUpdate = async (id: string, data: Partial<ExtendedVehicleIssue>) => {
    try {
      const issueRef = doc(db, 'vehicleIssues', id);
      await updateDoc(issueRef, data);
    } catch (error) {
      console.error('‚ùå Failed to update vehicle issue:', error);
    }
  };

  // ‚úÖ Search filter (local search tr√™n issues ƒë√£ fetch)
  const filteredIssues = search
    ? issues.filter(
        (issue) =>
          issue.vin?.toLowerCase().includes(search) ||
          issue.assignedTechnicianId?.toLowerCase().includes(search)
      )
    : issues;

  return {
    issues: filteredIssues,
    loading,
    handleUpdate,
    loadMore,
    hasMore,
  };
}
