'use client';

import { Dialog, DialogContent, DialogHeader, DialogTitle, DialogFooter } from '@/src/components/ui/dialog';
import { Button } from '@/src/components/ui/button';
import { CheckCircle, AlertCircle, Info, Trash2 } from 'lucide-react';
import { cn } from '@/src/lib/utils';

export type NotificationType = 'success' | 'error' | 'info' | 'confirm';

export interface NotificationDialogProps {
  open: boolean;
  type: NotificationType;
  title: string;
  description?: string;
  onClose: () => void;
  onConfirm?: () => void;
}

export default function NotificationDialog({
  open,
  type,
  title,
  description,
  onClose,
  onConfirm,
}: NotificationDialogProps) {

  const renderIcon = () => {
    const baseClass = "w-8 h-8 animate-pop";
    switch (type) {
      case 'success':
        return <CheckCircle className={cn(baseClass, "text-green-500")} />;
      case 'error':
        return <AlertCircle className={cn(baseClass, "text-red-500")} />;
      case 'info':
        return <Info className={cn(baseClass, "text-blue-500")} />;
      case 'confirm':
        return <Trash2 className={cn(baseClass, "text-orange-500")} />;
      default:
        return null;
    }
  };

  return (
    <Dialog open={open} onOpenChange={onClose}>
      <DialogContent className="w-full max-w-md sm:max-w-sm p-6 rounded-2xl space-y-5 pb-10">
        <DialogHeader className="flex items-center gap-3">
          {renderIcon()}
          <DialogTitle className="text-lg sm:text-xl">{title}</DialogTitle>
        </DialogHeader>

        {description && (
          <p className="text-sm sm:text-base text-gray-600 leading-relaxed">
            {description}
          </p>
        )}

        <DialogFooter className="mt-6 space-x-3 flex justify-end">
          {type === 'confirm' ? (
            <>
              <Button variant="ghost" className="w-full sm:w-auto" onClick={onClose}>
                Cancel
              </Button>
              <Button variant="destructive" className="w-full sm:w-auto" onClick={onConfirm}>
                Confirm
              </Button>
            </>
          ) : (
            <Button className="w-full sm:w-auto mt-2" onClick={onClose}>OK</Button>
          )}
        </DialogFooter>
      </DialogContent>
    </Dialog>
  );
}
